import React from 'react';
import {showAlert} from '../../Alert';
import SubmitDataHelpers from '../../helpers/SubmitDataHelpers';
import SortingHelpers from '../../helpers/SortingHelpers';
import TimeHelpers from '../../helpers/TimeHelpers';

import CourseScanStyle from '../../../css/courses/creatorView/CourseScanStyle';
import '../../../css/courses/creatorView/CourseScan.css';

class CourseScan extends React.Component{
    constructor(props){
        super(props);
        this.state = {};
        this.countTimeLeft = this.countTimeLeft.bind(this);
    }

    componentDidMount(){
        this.renderResults();

        this.interval = setInterval(() => {
            this.renderResults();
        }, 15000);

        this.countTimeLeft();
        let countTimeLeft = window.setInterval(this.countTimeLeft,1000);
        window.sessionStorage.setItem('countTimeLeft', countTimeLeft);

        CourseScanStyle(this.props.course.checkPointNo);
    }

    componentDidUpdate(){
        CourseScanStyle(this.props.course.checkPointNo);
    }

    componentWillUnmount(){
        clearInterval(this.interval);
    }

    render(){
        return (
            <div id="courseScan" className='courseScan px-sm-5 pt-5'>
                <h2 className='mt-5'>Live Results</h2>
                <h4 className='mb-5'>( {this.state.timeLeft} )</h4>
                <div id="resultsChart" className='resultsChart'>{this.state.content}</div>
            </div>
        );
    }

    renderResults(){
        let course = this.props.course;

        let helpers = new SubmitDataHelpers;
        helpers.postData('/results/getResults/', 
        {
            event: this.props.course.event,
            type: this.props.course.type,
        }).then((res) => {
            //console.log(res); // JSON data parsed by `response.json()` call
            if(res.error){
                //console.log(res.error);
                showAlert(res.error, 5000);
            } else {
                let sortingHelpers = new SortingHelpers;

                let highestScanNo = sortingHelpers.findMax(res, 'scanNo').scanNo;

                let tr = [];
                
                while (highestScanNo > 0){
                    let results = [];

                    res.forEach(r => {
                        if(r.scanNo == highestScanNo){
                            results.push(r);
                        }
                    })

                    let count = 0;
                    while(results.length > 0){
                        let td = [];
                        
                        let minResult = sortingHelpers.findMin(results, 'timeTaken');
                        //let shortestTimeTaken = minResult.timeTaken;

                        let timeHelpers = new TimeHelpers();
                        let finalTimeTaken = timeHelpers.sToHms(minResult.timeTaken).short;

                        td.push(
                            <div key={minResult.username} className='label d-sm-flex flex-sm-row justify-content-sm-end align-items-sm-center td col-sm-4 m-0 p-0 text-left text-sm-right'>
                                {minResult.username}<b></b>
                                {window.innerWidth > 576? '' : <br/>}
                                <div className='text-sm-center px-sm-3 mb-sm-0 d-sm-flex justify-content-sm-center align-items-sm-center'><b>
                                    {finalTimeTaken}
                                    <br/>{course.type == 'Score'? ' (Points: ' + minResult.score + ')' : ''}
                                </b></div>
                            </div>
                        );

                        let sortedRes = [];

                        res.forEach(r => {
                            if(r.username === minResult.username){
                                sortedRes.push(r);
                            }
                        });
                        
                        sortedRes = sortingHelpers.quickSort(sortedRes, 0, sortedRes.length - 1, 'scanNo');
                        let bar = [];
                        sortedRes.forEach(r => {
                            let h = parseInt(r.timeTaken/3600);
                            let m = parseInt((r.timeTaken - h*3600)/60);
                            let s = r.timeTaken - h * 3600 - m * 60;

                            h = h < 10? '0' + h : h;
                            h = h == 0? '' : h + ':';
                            m = m < 10? '0' + m + ':': m + ':';
                            s = s < 10? '0' + s : s;
                            let timeTaken = h + m + s;
                            
                            bar.push(
                                <div key={timeTaken} className='checkpoint tooltips d-flex align-items-center'>
                                    <div className=''></div>
                                    <span className="tooltiptext px-3 py-3">
                                        Number of Scans: {r.scanNo}
                                        <br/>Score: {r.score}
                                        <br/>Time Taken: {timeTaken}
                                        <br/>Speed: {(new TimeHelpers).sToHms(r.speed).short}
                                    </span>
                                </div>
                            );
                            
                        });
                        
                        td.push(
                            <div key={minResult.username + 'bar'} className='td bar col-sm-8 p-0 text-left d-flex align-items-center'>
                                {bar}
                            </div>
                        );

                        tr.push(
                            <div key={minResult.username + 'tr'} className='tr col-12 row p-0 m-0 pt-2'>{td}</div>
                        );
                        
                        for(let i = res.length -1; i >= 0; i--){
                            let r = res[i];
                            
                            if(r.username === minResult.username){
                                res.splice(res.indexOf(r), 1);
                                
                            }
                        }
                        //console.log(res);
                        results.splice(results.indexOf(minResult), 1);
                        
                        count++;
                        if(count > 1000){
                            //console.log("end");
                            break;
                        }
                    }

                    highestScanNo--;
                }

                let content = [];

                content.push(
                    <div key='table' className='table col-10 col-sm-12'>
                        <div className='tbody'>
                            {tr}
                        </div>
                    </div>
                )
                
                this.setState({content: content});
            }
        });
    }

    countTimeLeft(){
        let endTime = this.props.course.endTime;

        endTime = endTime.split(":");
        endTime = endTime[0]*3600 + endTime[1]*60;
        
        let date = new Date();
        let currentTime =  date.getHours()*3600 + date.getMinutes()*60 + date.getSeconds();
    
        let timeLeft = endTime - currentTime;

        let h = parseInt(timeLeft/3600);
        let m = parseInt((timeLeft - h*3600)/60);
        let s = timeLeft - h * 3600 - m * 60;

        h = h < 10? '0' + h : h;
        h = h == 0? '' : h + ' : ';
        m = m < 10? '0' + m + ' : ': m + ' : ';
        s = s < 10? '0' + s : s;
        
        timeLeft = h + m + s;

        this.setState({timeLeft: timeLeft});
    }
}

export default CourseScan;